.homePage {
  width: 100%;
  color: #F7B21F;
  height: calc(100vh - 300px);
  font-size: 22px;
  font-weight: bold;
  background-color: darken(black, 50%);

  .shadowForm {
    background-color: rgba(#222, .5);
    padding: 30px 120px;
    margin: auto 0;
    position: relative;
    height: 50%;

    .h1 {
      margin: auto 0;
      font-size: 42px;
      margin-right: 20px; }

    .orderInput {
      width: 250px;
      color: white; } } }

.carousel {
  margin-top: 10px;
  text-align: center;
  height: 260px;
  line-height: 260px;
  background: #F7B21F;
  overflow: hidden; }

